import { mapGetters } from 'vuex';
import axios from 'axios';
import moment from 'moment';


const cancelTokenSource = axios.CancelToken.source();

export default {
  layout: 'edit',
  middleware: 'auth',
  computed: {
    ...mapGetters(['isAuthenticated', 'loggedInUser']),
  },
  created() {
	 this.url_list = '/edit/intervenants/';
	 
	 if (typeof this.$route.query.id_intervenant === 'undefined') {
		 this.id_intervenant = 0;
	 }
	 else  this.id_intervenant = parseInt(this.$route.query.id_intervenant);
	 	 	 
  }
  ,
  mounted() {
	  if (this.id_intervenant == '0') this.id_intervenant = this.getNextId(this.source)
	  else {
		  this.getIntervenant();
	  }

  },
  data() {
    return {
	  url_server:this.$store.state.url_server,
	  id_font_app: this.$auth.user.id,
	  user_app: this.$auth.user.username,
	  initModeForm:'include',
	  source:'intervenant',
	  isNew:false,
	  url_list: '',
	  isLoadForm:false,
	  id_intervenant: 0,
      nom:'',
      prenom:'',
      biographie:'',
      signature:'',
	  intervenant:[],
	  intervenants:[],
	  displayComposant : false,
	  displayEditSeo : false,
	  isDisplayMedia:false,
	  editorOption: {
          // Some Quill options...
          theme: 'snow',
          modules: {
            toolbar: [
              ['bold', 'italic', 'underline', 'strike'],
              ['blockquote', 'code-block'],
              ['link','code'],
              ["source"]
            ]
          }
        },
	  upload_signature_titre_file:"Signature",
	  upload_signature_label_file:'choisir le visuel',
	  upload_signature_accept_file : '.jpg,.jpeg,.png,.gif',
	  upload_signature_cible:'mediaCible1',
    }
  }
  ,
  methods: {
	getIntervenant: function()
	 {
       axios.get(this.url_server + "/getIntervenant?id_intervenant=" + this.id_intervenant)
       .then(response => {
	       this.intervenant = response.data.liste;
		   if (typeof this.intervenant.nom !== 'undefined') {
		       this.id_intervenant = this.intervenant.id_intervenant;
		       this.nom = this.intervenant.nom;
		       this.prenom = this.intervenant.prenom;
		       this.biographie = this.intervenant.biographie;
		       this.signature = this.intervenant.signature;
		    }
		    
		    this.isLoadForm = true;
	       this.displayComposant = true;
	   })
       .catch(error => {
	       this.id_intervenant = 0;
		   displayErreur("error loading invité")
      })
    }
    ,
    submit() {
	   clearRequired();
	   this.hideErreur();

	   let data = { 
		id_intervenant: this.id_intervenant,
		nom: this.nom,
		prenom: this.prenom,
		biographie: this.biographie,
		signature: this.signature,
        isNew: this.isNew,
      };

	  var isFromSeo = false;
      var elems = document.querySelectorAll(".required_field");
	  [].forEach.call(elems, function(el) {
		    if ((el.value == '')) {
			   if (el.classList.contains("fromSeo")) isFromSeo = true;
			   el.classList.add("required");
			}
		});
		
		if(checkRequired()) {
			if (isFromSeo) this.$refs.editSeoEl.isShowSeo = true;
			displayErreur("Les champs en rouge sont requis");
		}
		else {
			disabledActions();
			  axios.post(this.url_server + "/addIntervenant", data)
			   .then(response => {
				   this.id_intervenant = response.data.id_intervenant;
				   this.$refs.editSeoEl.saveSeo();
			   })
			   .catch(error => {
				  enabledActions();
				  displayErreur("Error adding invité : " + error)
			   })
			
		}
       
    },
    getNextId(table) {
	    axios.get(this.url_server + "/getNextId?table=" + table)
	       .then(response => {
		       this.id_intervenant = response.data.id;
		       this.isNew = true;
		       this.getIntervenant();
		    })
		   .catch(error => {
			   displayErreur('Error getting nextId')
	      })
    }
    ,
    confirmSubmit() {
	    this.$router.push('/edit/intervenants/');
	}
    ,
	updateSeo(){
	  var nom = '';
	  if (Array.isArray(this.nom)) nom = this.nom[0];
	  else nom = this.nom;

	  this.$refs.editSeoEl.updateInputSeo(nom);
	  
	}
	,
    hideErreur: function()
    {
	    hideErreur();
    }
    ,
    cancelForm: function(url)
    {
	    this.$router.push(url);
    }
    ,
    initForm: function(event)
    {
	   if (event.target.classList.contains("required")) event.target.classList.remove("required");
	   enabledActions();
	   hideErreur();
    }
    
  }
}
